@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Rochester&display=swap');

*{
  margin:0;
  padding: 0;
  box-sizing: border-box;
}
:root{
  --primary-color:blanchedalmond;
  --secondary-color:rgba(228, 190, 140, 0.25);
  --black-color:black;
}

body {
  font-family: 'Poppins', sans-serif;
  background-color: var(--primary-color);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
a{
  text-decoration:none;
}

::-webkit-scrollbar{
  width: 10px;
}
::-webkit-scrollbar-thumb{
  background: var(--secondary-color);
}

/* ======================= REUSABLE CSS ======================= */
.d-flex{
  display: flex;
}

/* ===================== NAVBAR ==================== */
.navbar{
  width: 100%;
  height: 80px;
}
.nav__container{
  max-width: 1500px;
  margin:0 auto;
  height: 100%;
  width: 100%;
  position: relative;
  justify-content: space-around;
  align-items: center;
}
.nav__logo__container{
  position: relative;
  top:-2px;
  justify-content: center;
  align-items: center;
}
.nav__logo{
  font-family: 'Rochester', sans-serif;
  font-size:2rem;
  color:var(--black-color);
}
.nav__list{
  align-items: center;
  justify-content: space-around;
  padding:0 12px; 
}
.nav__item{
  padding:0 10px;
  list-style: none;
}
.nav__link{
  color:var(--black-color);
  transition: .2s;
}
.nav__link:hover{
  color:var(--secondary-color);
}
.search__input{
  border:2px solid brown;
  background-color: transparent;
  outline:none;
  border-radius: 9px;
  height: 20px;
  color:brown;
  padding-left: 2px;
  margin-left: 10px;
}
.search__button{
  background-color: var(--secondary-color);
  height:30px;
  justify-content: center;
  align-items: center;
  width: 30px;
  margin-left:10px;
  border-radius: 50%;
  color:black;
}
.search__button:hover{
  color:brown;
}
.burger{
  display: none;
}

/* ==================== NEWS ==================== */


.news__container{
  height: 85vh;
  width: 100%;
  max-width: 1100px;
  margin:0 auto;
  padding:10px 20px;
  flex-direction: column;
}
.heading{
    padding:0 20px;
    justify-content: space-between;
    align-items: center;
}
.news__type{
  font-family: 'Rochester', sans-serif;
  font-size:2rem;
}
.news__body{
  height:500px;
  gap:2rem;
  margin-top:30px;
  padding:0 20px;
  overflow-y: auto;
  flex-wrap: wrap;
}
.card{
  width: 300px;
  height: 330px;
  border-radius: 5px;
  background-color: var(--secondary-color);
  overflow:hidden;
}
.card div{
  height: 49%;
}
.news__img{
  background-color: grey;
}
.thumbnail{
  width: 100%;
  height: 100%;
}
.news__content{
  position: relative;
  padding:10px 20px;
}
.title{
  font-size: 1rem;
  font-weight: 500;
}
.readmore{
  color:brown;
  position: absolute;
  bottom:0.5rem;
  left:1.2rem;
  transition: .2s;
}
.readmore:hover{
  color:var(--secondary-color);
}

/* ================== MEDIA QYUERY ================== */
@media screen and (max-width:700px){
    nav{
      z-index:10;
    }
    .nav__container{
      justify-content: space-between;
      padding:0 10px;
    }
    .burger{
      z-index:200;
      display: block;
      cursor: pointer;
    }
    .nav__list{
      z-index:100;
      flex-direction: column;
      position: absolute;
      height: 90vh;
      gap:2px;
      width: 100%;
      top:-1000%;
      left:0;
      transition: .5s;
      background-color: var(--primary-color);
      opacity: 1;
    }
    .show-menu{
      top:0;
    }
    .news__body{
      justify-content: center;
      align-items: center;
    }
    .card{
      width:calc(100% - 20px);
    }
    .date{
      display:none;
    }
}